.home-slideshow {
  width: 1248px;
  display: grid;
  grid-template-columns: 75% 25%;
  grid-template-rows: auto;
  grid-template-areas: "active sidebar";
}

.home-slideshow__active {
  padding: 16px 0 16px 16px;
  background: #2a2a2a;
  grid-area: "active";
}

.home-slideshow__sidebar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-area: "sidebar";
}

.home-slideshow-item {
  height: 100%;
}

.home-slideshow-item__image {
  height: 100%;
}

.home-slideshow-sidebar-item {
  height: 25%;
}

.home-slideshow-sidebar-item__content {
  display: -webkit-flex;
  display: flex;
  color: white;
  background-color: #2a2a2a;
  -webkit-align-items: center;
          align-items: center;
}

.home-slideshow-sidebar-item__img {
  width: 50%;
  padding: 16px;
}

.home-slideshow-sidebar-item__progressbar {
  height: 3px;
  background-color: #2a2a2a;
}

.home-slideshow-sidebar-item__progressbar-active {
  width: 0;
  height: 3px;
  background-color: white;
  -webkit-animation: forwards 5s sidebar__progressbar;
          animation: forwards 5s sidebar__progressbar;
}

@-webkit-keyframes sidebar__progressbar {
  to {
    width: 100%;
  }
}

@keyframes sidebar__progressbar {
  to {
    width: 100%;
  }
}

